import { Component } from '@angular/core';
import { SignInService } from './sign-in.service';

@Component({
    selector: 'app-sign-in',
    template: `
    <form (ngSubmit)="onSubmit(formSignIn);" #formSignIn="ngForm">
        <input 
            placeholder="Email" 
            ngModel 
            #txtEmail="ngModel" 
            name="email" 
            required
            email>
        <p *ngIf="txtEmail.touched && txtEmail.errors?.required">Email is required</p>
        <p *ngIf="txtEmail.touched && txtEmail.errors?.email">Email is not valid</p>
        <br><br>
        <input 
            type="password" 
            placeholder="Password" 
            ngModel
            #txtPassword="ngModel" 
            name="password"
            minlength="6">
        <br><br>
        <label><input type="checkbox" [ngModel]="false" name="football">Football</label><br>
        <button >Submit</button>
    </form>
    <p>{{txtEmail.errors | json}}</p>
    <p>{{txtPassword.errors | json}}</p>
    <p>{{formSignIn.value | json}}</p>
    `,
    providers: [SignInService]
})

export class SignInComponent {
    email = '';
    password = '';
    constructor(private signInService: SignInService) { }

    onSubmit(formSignIn) {
        this.signInService.sendPost(formSignIn.value)
            .then(result => console.log(result))
            .catch(err => console.log(err));
    }
}